import { CrossSellingPointer } from "../../others";
import { RPM } from ".";
import $ from "jquery";
const crossRpmMove = () => CrossSellingPointer.isActionAvailable(RPM, "move") && window.rml?.hooks.register("attachment/move/finished", (ids, to) => {
  new CrossSellingPointer($(`.rml-container:visible li[data-li-id="${to}"]`), RPM, "move");
});
export { crossRpmMove };